﻿.entry {
  position: relative;
  padding-top: 34px;
  background: url(../../images/header/bg-mobile.png) center no-repeat;
  background-size: cover;

  @media (min-width: $md) {
    background-image: url(../../images/header/bg-tablet.jpg);
  }
  @media (min-width: $lg) {
    background-image: url(../../images/header/bg-desktop.jpg);
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 10px;
    background-color: #efc7c2;
    box-shadow: 4px 5px 13px rgba(80, 87, 94, 0.1);

    @media (min-width: $lg) {
      content: none;
    }
  }
  &-wrapper {
    width: 100%;
    max-width: calc(100% - 48px);
    margin-left: auto;
    margin-right: auto;

    @media (min-width: $md) {
      max-width: calc(100% - 100px);
    }

    @media (min-width: $xl) {
      max-width: 1220px;
    }

    &--header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 36px;

      @media (min-width: $md) {
        margin-bottom: 70px;
      }

      .entry-header--logo {
        a {
          display: block;
        }
        img {
          width: 122px;
          height: 34px;

          @media (min-width: $md) {
            width: 179px;
            height: 50px;
          }
          @media (min-width: $xl) {
            width: 214px;
            height: 60px;
          }
        }
      }

      .entry-header--nav {
        // img {
        //   width: 34px;
        //   height: 34px;
        // }

        @media (min-width: $lg) {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          a {
            font-family: "Sarabun", sans-serif;
            font-weight: 600;
            font-size: 15px;
            line-height: 19px;
            display: block;
            color: $brand-white;
            overflow: hidden;
            position: relative;

            &::before {
              content: "";
              position: absolute;
              left: -100%;
              bottom: 0;
              width: 100%;
              height: 1px;
              background-color: $brand-white;
              transition: 0.4s ease;
            }

            &:hover {
              color: darken($brand-white, 10%);

              &::before {
                left: 0;
                background-color: darken($brand-white, 10%);
              }
            }

            &:active {
              color: darken($brand-white, 20%);
              transition: 0s;

              &::before {
                background-color: darken($brand-white, 20%);
              }
            }
          }

          li {
            &:first-of-type {
              margin-left: 0;
            }
            @media (min-width: $lg) {
              margin-left: 30px;
            }

            @media (min-width: $xl) {
              margin-left: 40px;
            }
          }

          .entry-nav {
            &--menu {
              display: flex;
              flex-direction: row;
              align-items: center;
            }
            &--cabinet {
              display: flex;
              flex-direction: row;
              align-items: center;

              @media (min-width: $lg) {
                margin-left: 100px;
              }

              @media (min-width: $xl) {
                margin-left: 217px;
              }

              li {
                &:last-of-type {
                  margin-left: 25px;
                  a {
                    width: 100px;
                    padding-top: 11px;
                    padding-bottom: 11px;
                    font-size: 14px;
                    line-height: 18px;
                  }
                }
              }

              .btn {
                &::before {
                  content: none;
                }
              }
            }
          }
        }
      }
    }

    &--body {
      @media (min-width: $lg) {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
      }

      h1 {
        font-family: "Hind Siliguri", sans-serif;
        font-weight: bold;
        font-size: 24px;
        line-height: 1.33;
        color: $brand-white;
        text-shadow: 0px 1px 3px rgba(27, 48, 110, 0.5);
        margin-bottom: 16px;
        display: block;

        @media (min-width: $md) {
          font-size: 40px;
          margin-bottom: 24px;
        }

        @media (min-width: $lg) {
          width: 100%;
          max-width: 430px;
          font-size: 36px;
          line-height: 1.24;
          margin-bottom: 31px;
        }

        @media (min-width: $xl) {
          width: 100%;
          max-width: 586px;
          font-size: 50px;
        }
      }

      .entry-body--video {
        margin-bottom: 16px;
        overflow: hidden;
        border-radius: 7px;

        @media (min-width: $md) {
          margin-bottom: 48px;
        }

        @media (min-width: $lg) {
          margin-bottom: 0;
          width: 430px;
          height: 250px;
        }

        @media (min-width: $xl) {
          width: 529px;
          height: 317px;
        }
      }

      ul {
        margin-bottom: 32px;
        @media (min-width: $md) {
          margin-bottom: 48px;
        }
        @media (min-width: $lg) {
          margin-bottom: 67px;
          max-width: 402px;
        }

        li {
          position: relative;
          font-family: "Sarabun", sans-serif;
          padding-left: 27px;
          margin-bottom: 8px;
          font-weight: 400;
          font-size: 12px;
          line-height: 1.5;
          color: $brand-white;

          @media (min-width: $md) {
            font-size: 18px;
            padding-left: 46px;
            margin-bottom: 17px;
          }

          @media (min-width: $lg) {
            font-size: 20px;
            line-height: 1.6;
            margin-bottom: 14px;
          }

          &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 17px;
            height: 17px;
            background: url(../../images/header/icon-check.png) center no-repeat;
            background-size: contain;

            @media (min-width: $md) {
              width: 28px;
              height: 28px;
            }
          }
        }
      }

      .btn {
        margin-bottom: 28px;
        @media (min-width: $md) {
          margin-bottom: 58px;
        }
        @media (min-width: $lg) {
          margin-bottom: 155px;
        }
      }
    }

    &--footer {
      @media (min-width: $lg) {
        position: relative;
        background-color: $brand-white;
        border: 10px solid #efc7c2;
        border-right: 0;
        padding-top: 26px;
        padding-bottom: 27px;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 100%;
        max-width: 800px;
        box-sizing: content-box;

        &::before {
          content: "";
          position: absolute;
          left: -70px;
          top: -10px;
          bottom: 0;
          width: 90px;
          height: 99px;
          background: url(../../images/header/entry-footer--bg.png) center
            no-repeat;
          background-size: 100% 100%;
        }
      }
      @media (min-width: $xl) {
        max-width: 1200px;

        &::before {
          height: 112px;
        }
      }

      ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        @media (min-width: $lg) {
          justify-content: center;
        }
        li {
          opacity: 0.6;
          display: block;
          width: 100%;
          margin-bottom: 25px;
          max-width: 26%;
          transition: 0.3s ease;
          &:hover {
            opacity: 1;
          }
          @media (min-width: $md) {
            max-width: 12%;
          }
          @media (min-width: $lg) {
            margin-bottom: 0;
            max-width: 10%;
            margin-left: 50px;
            opacity: 1;
            &:first-of-type {
              margin-left: 0;
            }
          }
        }
        a {
          display: block;
        }
      }

      p {
        display: none;

        @media (min-width: $md) {
          display: block;
          font-family: "Sarabun", sans-serif;
          font-weight: 500;
          font-size: 12px;
          margin-bottom: 48px;
          line-height: 2;
          color: $brand-white;
        }
        @media (min-width: $lg) {
          display: none;
        }
      }
    }

    .plyr__video-wrapper--fixed-ratio {
      @media (min-width: $lg) {
        padding-bottom: 317px !important;
      }
    }

    .plyr--stopped.plyr__poster-enabled .plyr__poster {
      @media (min-width: $lg) {
        background-image: url(../../images/video-poster--desktop.png) !important;
        background-size: 100% 250px;
        background-position: top;
      }
      @media (min-width: $xl) {
        background-size: 100%;
        background-position: center;
      }
    }
  }
}
